import { shape, string } from 'prop-types';
import React, { useCallback } from 'react';
import { useSelector } from 'react-redux';

import { Page, Marker } from 'components';

const Job = ({ pageContext: { id } }) => {
  const selector = useCallback(({ jobs }) => jobs.find(job => job.id === id), [
    id,
  ]);
  const { title, description, company, employment_type } = useSelector(
    selector
  );

  return (
    <Page title={title} description={description}>
      <h1>{title}</h1>
      <dl>
        <dt>Description</dt>
        <dd>{description}</dd>
      </dl>
      <dl>
        <dt>Company</dt>
        <dd>{company}</dd>
      </dl>
      <dl>
        <dt>Type</dt>
        <dd>{employment_type}</dd>
      </dl>
      <Marker id={id} />
    </Page>
  );
};

Job.propTypes = {
  pageContext: shape({
    id: string.isRequired,
  }).isRequired,
};

Job.defaultProps = {};

export default Job;
